import React from 'react'
import {produtos} from './../data/produtos.js'
import Produto from './Produto.js'
import style from './../css/Produto.module.css'
import banner from '../imagems/dia.png'
const Produtos = () => {
    return (
        <section className = {style.containerGeral}>
            <div className = {style.containerProducts}>
                <h1 className = {style.tittleProducts}>Mais Procurados pelos usuarios</h1>
                    <section className = {style.containerProdutosProcurados}>
                    {
                        produtos.map((produto , index) => {
                            return <Produto {...produto} produto = {produto}  key ={produto.nome}  />
                        })
                    }
                    </section>
                    
                    <section className = {style.banner}>
                        <img src={banner}></img>
                    </section>
            </div>
        </section>
    )
}

export default Produtos
