import React from 'react'
import style from '../../css/Header.module.css'
import { GrCart } from 'react-icons/gr';
import { IoPersonOutline } from "react-icons/io5";
import { MdFavoriteBorder , MdFavorite} from "react-icons/md"
import useHover from '../../hooksPersonalizados/Header/useHover'

const UserHeader = ({value}) => {
    let {hover , favoriteHover ,leave} = useHover();
    if(value){
        return (

            <div className = {style.containerPerfil}>
           
                <div className = {style.minhaconta}>
    
                    <div className = {style.perfilLogado}> </div>

                        <div  onMouseLeave ={leave}onMouseMove ={ hover} className = {style.favoritos}>
                            {favoriteHover  ?  <MdFavorite className = {style.favoriteHover}/> : <MdFavoriteBorder/>}
                        </div>

                        <div className = {style.carrinho}>
                        <GrCart/>
                        <div className = {style.containerQtdCarrinho}>
                            22
                        </div>
                    </div>
                </div>
 
            </div>
        )
    }
    else{
     return(
        <div className = {style.containerPerfil}>
           
           <div className = {style.minhaconta}>

                <div className = {style.containerInfoMobile}>
                    <div className = {style.iconMinhaconta}>
                            <IoPersonOutline/>
                    </div>
                            
                    <div className = {style.textMinhaConta}>
                        <p  className = {style.pLogin}>
                        Entre ou Cadastre-se
                        </p>
                        <p  className = {style.pLoginAcess}>para ver seus pedidos</p>
                    </div>
                
                </div>
       

                <div className = {style.carrinho}>
                    <GrCart/>
                    <div className = {style.containerQtdCarrinho}>
                        22
                    </div>
                </div>

            </div>


        </div>
     )
    }
 
}

export default UserHeader
