import React from 'react'
import style from './../css/Produto.module.css'


function parcelas(valor){
    const total = valor / 12
    return total.toFixed(2); 
}

const Produto = ({nome,valor,url}) => {
    return (
        <article className = {style.box_product}>
           <div className = {style.fotoProduto}>
               <img alt = {nome}src={url}/>
           </div>
           <div className = {style.nomeProduto}>
               {nome}
           </div>
           <div>
               <div className = {style.valorProduto}>
                   Valor total : R$
                   {valor}
                </div>

               <div className = {style.parcelamentoProduto}>
                  Em ate 12x de R$ {parcelas(valor)} sem juros
               </div>
           </div>
           <button className ={style.buttonComprarProduto}>Comprar</button>
        </article>
    )
}

export default Produto
