import React from 'react'
import UserHeader from './UserHeader.js'
import {ContextUser} from '../contexts/UserContext'
import BarraPesquisa from '../Header/BarraPesquisa'
import style from '../../css/Header.module.css'
const Info = ({menu}) => {
    const {logado} = React.useContext(ContextUser)
    let {matches} = window.matchMedia('(max-width:768px)');

    
    return (
        <div className = {matches ? menu ? style.containerInfo:style.containerInfoOff : style.containerInfo}>
            <div className = {style.infos}>
                <BarraPesquisa/>
                <UserHeader value = {logado}/>
            </div>
        </div>
    )
}

export default Info
