import React from 'react'

const useHover = () => {
    let [favoriteHover , setFavoriteHover] = React.useState(false);


    const hover = React.useCallback(()=>{
        setFavoriteHover(true);
    },[favoriteHover])

    const leave = React.useCallback(()=>{
        setFavoriteHover(false);
    },[favoriteHover])
    

    return{
        favoriteHover,
        setFavoriteHover,
        hover,
        leave,
    }
}

export default useHover
