import React from 'react'
import style from '../../css/Header.module.css'
const InputPesquisa = () => {
    return (
            <form>
                 <div className={style.containerInput}>  
                    <input type="search"/>
                    <button className = {style.buttonPesquisar} type="submit">Pesquisar</button>
                </div>
            </form>
    )
}

export default InputPesquisa
