import React from 'react'
import style from '../css/carouselBanner.module.css'
import banner from '../imagems/banner3.png'
const Banners = () => {
    let [active,setActive] = React.useState(0);
    let [position,setPosition] = React.useState(0);
    let contentRef = React.useRef();

    React.useEffect(() => {
        let {width} = contentRef.current.getBoundingClientRect()
        setPosition(-(width * active))
    },[active])
    function slidePrev(){
            if(active>0) setActive(active - 1)
    }
    function slideNext(){
        if(active<2) setActive(active + 1)
    }
    return (
        <section className = {style.carouselBanner}>
            
            
            <div ref={contentRef} style={{transform :`translateX(${position}px)`}} className = {style.divBanner}>
                <img className = {style.item} src = {banner}/>
                <img className = {style.item}src = {banner}/>
                <img className = {style.item}src = {banner}/>
            </div> 

                <button onClick = {slidePrev}className = {style.btnPrev}>prev</button>
                <button onClick = {slideNext} className = {style.btnNext}>next</button>
        </section>
    )
}

export default Banners
