import React from 'react'
import PromoHeader from './PromoHeader.js'
import Logo from './Logo.js'
import {GiHamburgerMenu} from "react-icons/gi"
import style from '../../css/Header.module.css'
import Info from '../Header/Info'
import useHamburguer from '../../hooksPersonalizados/Header/useHamburguer'



const Header = () => {
    let {menu,abrir} = useHamburguer();
    return (
        <header>
              <PromoHeader/>
           
                    <article className = {style.HeaderInfos}>
                        <Logo/>
                        <div  onClick = {abrir}  className ={style.hamburguer}><GiHamburgerMenu/></div>
                        <Info menu = {menu}/>
                          

                    </article>

            <article className = {style.headerArticle}>
                <nav className = {style.headerNav}>
                    <ul className = {style.headerUl}>
                        <li>
                            Produtos
                        </li>

                        <li>
                            Pegue seu Cupom
                        </li>

                        <li>
                            Faca o seu cartao
                        </li>

                        <li>
                            Venda seus produtos
                        </li>

                        <li>
                            Promocoes
                        </li>

                        <li>
                            Baixe o app
                        </li>

                        <li>
                            Entre em contato
                        </li>

                    </ul>
                </nav>
          </article>

        </header>   
    )
}

export default Header
